<template>
<div class="container">
  <div class="row">
    <div class="col-md-4">
      <div class="top20 hidden-xs hidden-sm"></div>
      <div class="panel panel-primary panelDoggy">
        <div class="panel-heading">
          <div class="title">Iniciar Sesion</div>
        </div>
        <div class="panel-body">
          <form @submit.prevent="login">
            <small class="text-danger" v-if="errors.errors">{{errors.errors}}</small>
            <div class="form-group">
              <label>Email</label>
              <input placeholder="correo@correo.com" type="text" v-model="form.email" class="form-control inputDoggy" required>
            </div>
            <div class="form-group">
              <label>Password</label>
              <input placeholder="password" type="password" v-model="form.password" class="form-control inputDoggy" required>
            </div>
            <br><button class="btn btn-primary">Acceder</button>
          </form>
        </div>
      </div>
    </div>
  </div>
</div>
</template>
<script>
import axios from 'axios'
import Vue from 'vue'

export default {
  name: 'Login',
  data () {
    return {
      form: {},
      redirect: '/',
      errors: {}
    }
  },
  methods: {
    login () {
      var vm = this
      window.$('.btn-primary').button('loading')
      axios.post('http://mrdoggy.ppbox.us/v1/login', this.form)
        .then(function (response) {
          if (response.data) {
            vm.$router.push(vm.redirect)
          }
        })
        .catch(function (error) {
          window.$('.btn-primary').button('reset')
          Vue.set(vm.$data, 'errors', error.response.data)
        })
    }
  }
}
</script>



