                <div class="form-group">
                  <select  class="form-control selectDoggy" required v-model="form.roll">
                    <option disabled value="" selected>Seleccione el roll...</option>
                    <option value="1">Paseador</option>
                    <option value="2">Cliente</option>
                  </select>
                  <small class="text-danger" v-if="errors.name">{{errors.name}}</small>
                </div>
                <div class="form-group">
                  <input placeholder="John Doe" type="text" v-model="form.name" class="form-control inputDoggy" required>
                  <small class="text-danger" v-if="errors.name">{{errors.name}}</small>
                </div>
                <div class="form-group">
                  <input placeholder="correo@correo.com" type="text" v-model="form.email" class="form-control inputDoggy" required>
                  <small class="text-danger" v-if="errors.email">{{errors.email}}</small>
                </div>
                <div class="form-group">
                  <input placeholder="password" type="password" v-model="form.password" class="form-control inputDoggy" required>
                  <small class="text-danger" v-if="errors.password">{{errors.password}}</small>
                </div>
                <div class="form-group text-left">
                  <input type="checkbox" v-model="form.password" id="test1" required>
                  <label for="test1">Acepto terminos y condiciones.</label>
                </div>
                <br><button class="btn btn-primary btn-Doggy btn-block">Registrarse</button>
            </form>
