          <img class="img-responsive imgBack" src="../../assets/img/contact.png">
          <div class="top80 hidden-xs hidden-sm"></div>
          <div class="row bottom50">
            <div class="col-md-offset-1 col-md-6 textFront marginLeft text-left">
                <h1 class="white"><strong>Contacto</strong></h1>
                <strong class="textSmall whiteColor">Envianos tu mensaje</strong>
                <form class="top20">
                    <div class="form-group">
                      <input placeholder="Email" type="text" class="form-control inputDoggySquare" required>
                    </div>
