                </div>
                <div class="col-md-5 text-center">
                    <strong class="textMedium whiteColor">Mr Doggy</strong>
                    <p class="whiteColor psinMarginBottom">instagram.com/MrDoggy</p>
                    <p class="whiteColor">twitter.com/MrDoggy</p>
                </div>
                <div class="col-md-10 text-center">
                    <p class="whiteSmall text-center top20">Tel: 322 256 7845/45784</p>
                </div>
                <div class="col-md-2 text-center"></div>
                <div class="col-md-10 top20 bottom98 text-center">
                    <div class="table-responsive">
                     <iframe src="https://www.google.com/maps/embed?pb=!1m18!1m12!1m3!1d254508.51641089004!2d-74.24789567650163!3d4.648283716972999!2m3!1f0!2f0!3f0!3m2!1i1024!2i768!4f13.1!3m3!1m2!1s0x8e3f9bfd2da6cb29%3A0x239d635520a33914!2sBogot%C3%A1%2C+Colombia!5e0!3m2!1ses!2ses!4v1491341456805" width="400" height="200" frameborder="0" style="border:0" allowfullscreen></iframe>
                    </div>
                </div>
            </div>
        </div>

        <!--- modal login --->
        <div class="modal fade" id="modal-login" tabindex="-1" role="dialog" aria-labelledby="exampleModalLabel" aria-hidden="true">
          <div class="modal-dialog" role="document">
            <div class="modal-content modalBodyMrDoggy">
              <div class="modal-body">
                 <div class="row">
                 <div class="col-md-8">
                 <form @submit.prevent="login">
                    <small class="text-danger" v-if="errors.errors">{{errors.errors}}</small>
                    <div class="form-group">
                      <label>Email</label>
                      <input placeholder="correo@correo.com" type="text" v-model="user.email" class="form-control inputDoggy" required>
                    </div>
                    <div class="form-group">
                      <label>Password</label>
                      <input placeholder="password" type="password" v-model="user.password" class="form-control inputDoggy" required>
                    </div>
                    <br><button class="btn btn-primary btn-Doggy">Acceder</button>
                 </form>
                 </div>
                 <div class="col-md-4 contact text-center hidden-xs">
                    <div class="col-md-4 col-md-offset-2">
                    <div class="top80 bottom80">
                    <img class="img-responsive imgMedium" src="../../assets/img/icono_blanco.png">
                    </div>
