</main>
</template>

<script>
import axios from 'axios'
import Vue from 'vue'

export default {
  name: 'Home',
  data () {
    return {
      user: {},
      form: {},
      errors: {},
      rolls: [],
      redirect: '/'
    }
  },
  beforeMount () {
    this.fetchData()
  },
  watch: {
    '$route': 'fetchData'
  },
  methods: {
    fetchData () {
      var vm = this
      axios.get(`http://mrdoggy.ppbox.us/v1/user_rolls`)
        .then(function (response) {
          vm.rolls = response.data
        })
        .catch(function (error) {
          console.log(error)
        })
    },
    register () {
      window.$('.btn-primary').button('loading')
      axios.post('http://mrdoggy.ppbox.us/v1/users', this.form)
        .then(function (response) {
          console.log(response)
        })
        .catch(function (error) {
          console.log(error)
          window.$('.btn-primary').button('reset')
        })
    },
    login () {
      var vm = this
      window.$('.btn-primary').button('loading')
      axios.post('http://mrdoggy.ppbox.us/v1/login', this.user)
        .then(function (response) {
          if (response.data) {
            vm.$router.push(vm.redirect)
            window.$('#modal-login').modal('hide')
          }
        })
        .catch(function (error) {
          window.$('.btn-primary').button('reset')
          Vue.set(vm.$data, 'errors', error.response.data)
        })
    }
  }
}
</script>




