<template>
<div class="row">
  <div class="col-md-12">
     <h2>{{ title }}</h2>
     <div class="col-md-3" v-for="plan in plans">
        <img :src="plan.logo" class="img-responsive">
        <p><strong>{{ plan.name }}</strong></p>
        <p>$ {{ plan.price }}</p>
     </div>
  </div>
</div>
</template>

<script>
import axios from 'axios'

export default {
  name: 'HomePlan',
  data () {
    return {
      plans: [],
      title: 'Plans'
    }
  },
  beforeMount () {
    this.fetchData()
  },
  watch: {
    '$route': 'fetchData'
  },
  methods: {
    fetchData () {
      var vm = this
      axios.get(`http://mrdoggy.ppbox.us/plans`)
         .then(function (response) {
           vm.plans = response.data
         })
         .catch(function (error) {
           console.log(error)
         })
    }
  }
}
</script>




